<template>
  <v-container fluid>
    <v-card>
      <v-toolbar color="toolbarColor" dense dark>
        <v-toolbar-title>Orders</v-toolbar-title>
      </v-toolbar>
      <v-card-text>
        <v-row>
          <v-col cols="12" v-if="orders.length <= 0">
            <v-alert border="top" colored-border type="info" elevation="2"
              >No Orders Yet</v-alert
            >
          </v-col>

          <v-col cols="12" v-else>
            <v-text-field label="Search" v-model="search"></v-text-field>
            <v-data-table
              :items-per-page="2000"
              :headers="headers"
              :items="orders"
              :search="search"
            >
              <!-- action template -->
              <template v-slot:item.action="{ item }">
                <v-btn
                  color="editIcon"
                  text
                  :to="`/product/details/${item.url}`"
                  icon
                  ><v-icon>mdi-eye</v-icon>
                </v-btn>
              </template>
              <!-- end -->
            </v-data-table>
          </v-col>
        </v-row>
      </v-card-text>
    </v-card>
    <snackbar ref="snackbar"></snackbar>
  </v-container>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  computed: {
    ...mapGetters(["serverUrl"]),
  },
  data() {
    return {
      loader: false,
      dialog: false,
      search: null,
      orders: [],
      deleteItem: {},
      headers: [
        { text: "Name", value: "name" },
        { text: "Phone", value: "phone" },
        { text: "Email", value: "email" },
        { text: "Order Date", value: "orderDate" },
        { text: "Action", value: "action" },
      ],
    };
  },
  methods: {
    openModal(data) {
      this.deleteItem = Object.assign({}, data);
      this.dialog = true;
    },
    getData() {
      const url = "/order";
      const self = this;
      this.$store
        .dispatch("expressGet", url)
        .then((res) => {
          if (res.state) {
            self.orders = res.records;
          }
        })
        .catch((err) => {
          console.log(err, "error");
          this.$refs.snackbar.show(err, "red");
        });
    },
  },
  created() {
    this.getData();
  },
};
</script>

